@import url(https://fonts.googleapis.com/css?family=Quicksand:300);
body {
  height: 100vh;
  margin: 0;
  font-family: "Quicksand", sans-serif;
  font-size: 20px;
  background: #ff5f6d;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #ff5f6d, #ffc371);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ }

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%; }

.calculator {
  width: 20rem;
  height: 30rem;
  border-radius: 0.75rem;
  background: #eee;
  box-shadow: 0px 20px 120px -20px rgba(0, 0, 0, 0.7); }
  .calculator .display {
    display: flex;
    flex-wrap: wrap;
    text-align: right;
    overflow: hidden;
    height: calc(30% + 1px);
    width: 100%;
    background: #333a4d;
    border-top-left-radius: 0.75rem;
    border-top-right-radius: 0.75rem; }
    .calculator .display__current, .calculator .display__result {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      width: 100%;
      height: 50%;
      padding: 0 10px; }
    .calculator .display__current {
      color: rgba(255, 255, 255, 0.6); }
    .calculator .display__result {
      font-size: 52px;
      color: #eee; }
  .calculator .controls {
    display: grid;
    grid-template-columns: repeat(4, 25%);
    height: 70%; }
    .calculator .controls__item {
      font: 400 20px "Quicksand", sans-serif;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 0;
      border-bottom: 1px solid rgba(0, 0, 0, 0.05);
      border-right: 1px solid rgba(0, 0, 0, 0.05);
      cursor: pointer; }
      .calculator .controls__item:hover {
        background: #d5d5d5; }
      .calculator .controls__item--darker {
        background: #ccc; }
      .calculator .controls__item--orangeLight {
        background: #fea170; }
        .calculator .controls__item--orangeLight:hover {
          background: #fe9057; }
      .calculator .controls__item--orangeMedium {
        background: #f78d6c; }
        .calculator .controls__item--orangeMedium:hover {
          background: #f67a54; }
      .calculator .controls__item:nth-last-child(1) {
        border-bottom-right-radius: 0.75rem; }
      .calculator .controls__item:nth-last-child(4) {
        border-bottom-left-radius: 0.75rem; }

